/* Content */

div.menuItem {
    min-width: 30%;
    height: 240px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    margin: 0 7.5px 15px;
    overflow: hidden;
}

div.menuItem:hover {
    cursor: pointer;
}

div.cateBackground {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
}

