    transform: scale(1.1);
    transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
} */

div.cateItem {
    height: 15%;
    width: 13%;
    padding: 0 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    background-color: white;
    opacity: 0.7;
    position: absolute;
}

div.cateItem:hover {
    opacity: 0.9;
}

div.cateTitle {
    font-weight: bold;
    margin: 0 6px 0;
    font-size: 22px;
    color: #4a4a4a;
}

div.cateSubTitle {
    font-weight: lighter;
    font-size: 16px;
}